<template>
  <view class="page">

    <!-- È°∂ÈÉ®Ê†è -->
    <view class="header">
      <text class="title">ÁæéÈ£üÂúà</text>
      <text class="add" @click="onCreate">Ôºã</text>
    </view>

    <!-- Âä®ÊÄÅÂàóË°® -->
    <view class="feed">
      <view class="post" v-for="p in posts" :key="p.id">
        <view class="post-top">
          <image class="avatar" :src="p.avatar" mode="aspectFill" />
          <view class="author">
            <text class="name">{{ p.name }}</text>
          </view>
          <text class="more" v-if="!hasImages(p)" @click="toggleMenu(p)">¬∑¬∑¬∑</text>
        </view>

        <view class="content">{{ p.text }}</view>

        <view v-if="hasImages(p)" class="images" :class="'cols-' + gridCols(p)">
          <image v-for="(img, idx) in (p.images || [])"
                 :key="idx"
                 :src="img"
                 class="img"
                 mode="aspectFill"
                 @tap="onImageTap(p, idx)" />
        </view>
        <!-- ÂõæÁâá‰∏ãÊñπ‰∏ÄË°åÔºöÂ∑¶Êó∂Èó¥ÔºåÂè≥ÁúÅÁï•Âè∑ -->
        <view v-if="hasImages(p)" class="img-footer">
          <text class="time-badge">{{ displayTime(p) }}</text>
          <text class="menu-dot" @click.stop="toggleMenu(p)">¬∑¬∑¬∑</text>
        </view>

        <view class="menu-mask" v-if="p._menuOpen" @click="closeOverlayFor(p)"></view>
        <view class="actions-menu" v-if="p._menuOpen">
          <view class="am-item" @click="toggleLike(p)">{{ p.liked ? ('ÂèñÊ∂àÊî∂ËóèÔºà' + p.likes + 'Ôºâ') : ('Êî∂ËóèÔºà' + p.likes + 'Ôºâ') }}</view>
          <view class="am-item" @click="focusComment(p)">ËØÑËÆ∫Ôºà{{ getCommentCount(p.id) }}Ôºâ</view>
          <view class="am-item" @click="share(p)">ÂàÜ‰∫´</view>
        </view>
        <view class="actions" v-if="false">
          <view class="action" @click="toggleLike(p)">
            <text :class="['heart', p.liked ? 'liked' : '']">‚ù§</text>
            <text class="num">{{ p.likes }}</text>
          </view>
          <view class="action" @click="focusComment(p)"><text>üí¨</text><text class="num">{{ getCommentCount(p.id) }}</text></view>
          <view class="action" @click="share(p)"><text>üîó</text><text class="num">ÂàÜ‰∫´</text></view>
        </view>

        <!-- ËØÑËÆ∫Âå∫ -->
        <view class="comments">
          <view class="c-list" v-if="getComments(p.id).length">
            <view class="c-item" v-for="(c,i) in getComments(p.id)" :key="i">
              <text class="c-name">{{ c.name }}Ôºö</text>
              <text class="c-text">{{ c.text }}</text>
              <text class="c-time">{{ c.time }}</text>
            </view>
          </view>
          <!-- ËØÑËÆ∫ËæìÂÖ•‰ªÖÂú®ÁÇπÂáª‚ÄúËØÑËÆ∫‚ÄùÊó∂ÊòæÁ§∫ÔºåÂπ∂Ëá™Âä®ËÅöÁÑ¶ÔºåÂ§±ÁÑ¶‰∏îÊó†ÂÜÖÂÆπÊó∂Êî∂Ëµ∑ -->
          <view class="c-input compact" v-if="p._commenting">
            <input class="input compact"
                   :focus="focusMap[p.id]"
                   :placeholder="'ËØÑËÆ∫ @' + p.name"
                   v-model="inputMap[p.id]"
                   @confirm="submitComment(p)"
                   @blur="endComment(p)" />
            <button class="btn micro" @click="submitComment(p)">ÂèëÈÄÅ</button>
          </view>
        </view>
      </view>
    </view>

    <view class="page-mask" v-if="anyMenuOpen" @click="closeAllMenus"></view>
    <view style="height: 24rpx;" />
  </view>
</template>

<script>
export default {
  data() {
    return {
      seed: [
        {
          id: 'p1',
          name: 'Â∞èÈõÖÂé®Êàø',
          time: '2 Â∞èÊó∂Ââç',
          avatar: '/static/yuan_97e57f821c79b841651df5b413309328.jpg',
          text: '‰ªäÂ§©Â∞ùËØïÂÅö‰∫Ü‰º†ËØ¥‰∏≠ÁöÑÁΩëÁ∫¢ËäùÂ£´ËõãÁ≥ïÔºåÁ¨¨‰∏ÄÊ¨°ÂÅöÂ∞±ÊàêÂäü‰∫ÜÔºÅÂ•∂È¶ôÊµìÈÉÅ„ÄÅÂÖ•Âè£Âç≥ÂåñÔºåÂ§™Êª°Ë∂≥‰∫ÜÔΩû',
          images: ['/static/yuan_97e57f821c79b841651df5b413309328.jpg'],
          likes: 128
        },
        {
          id: 'p2',
          name: 'ËÄÅÂÖ´ÁöÑÁæéÈ£ü',
          time: '5 Â∞èÊó∂Ââç',
          avatar: '/static/yuan_97e57f821c79b841651df5b413309328.jpg',
          text: 'Âë®Êú´Âú®ÂÆ∂ÂÅö‰∫Ü‰∏ÄÈîÖÈ¶ôÂñ∑Âñ∑ÁöÑÁ∫¢ÁÉßËÇâÔºåËÇ•ËÄå‰∏çËÖª„ÄÅÁò¶ËÄå‰∏çÊü¥„ÄÇÁßòËØÄÂ∞±ÊòØË¶ÅÁî®ÂÜ∞Á≥ñÁÇíÁ≥ñËâ≤ÔºåÁÅ´ÂÄôÊéåÊè°Â•ΩÔºÅ',
          images: ['/static/yuan_97e57f821c79b841651df5b413309328.jpg','/static/yuan_97e57f821c79b841651df5b413309328.jpg'],
          likes: 256
        },
        {
          id: 'p3',
          name: 'ÁæéÈ£üËææ‰∫∫Â∞è‰∏Ω',
          time: '1 Â§©Ââç',
          avatar: '/static/yuan_97e57f821c79b841651df5b413309328.jpg',
          text: 'ÂàÜ‰∫´‰∏Ä‰∏™Ë∂ÖÁÆÄÂçïÁöÑÊó©È§êÈ£üË∞±ÔºÅÁâõÊ≤πÊûúÂêêÂè∏ÈÖçÁÖéËõãÔºåËê•ÂÖª‰∏∞ÂØåÂèàÁæéÂë≥Ôºå5 ÂàÜÈíüÂ∞±ËÉΩÊêûÂÆöÔΩû',
          images: ['/static/yuan_97e57f821c79b841651df5b413309328.jpg'],
          likes: 173
        }
      ],
      posts: [],
      commentsMap: {},   // { [postId]: [{name,text,time}] }
      inputMap: {},      // { [postId]: 'input text' }
      focusMap: {}       // { [postId]: boolean }
    }
  },
  onShow() {
    const local = uni.getStorageSync('social_posts') || []
    const likedSet = new Set(uni.getStorageSync('social_likes') || [])
    const merged = [...local, ...this.seed].map(p => ({
      ...p,
      liked: likedSet.has(String(p.id)),
      likes: Number(p.likes || 0),
      _menuOpen: false,
      _commenting: false
    }))
    this.posts = merged
    const cm = uni.getStorageSync('social_comments') || {}
    this.commentsMap = cm
    const im = {}
    const fm = {}
    merged.forEach(p => { im[p.id] = ''; fm[p.id] = false })
    this.inputMap = im
    this.focusMap = fm
  },
  onShareAppMessage(res) {
    return {
      title: 'ÂàÜ‰∫´‰∏ÄÁØáÁæéÈ£üÂúà‰ΩúÂìÅ',
      path: '/pages/social/index'
    }
  },
  computed: {
    anyMenuOpen() {
      return (this.posts || []).some(p => p && p._menuOpen)
    },
    anyOverlayOpen() {
      return (this.posts || []).some(p => p && (p._menuOpen || p._commenting))
    }
  },
  methods: {
    onImageTap(p, idx) {
      const urls = Array.isArray(p.images) ? p.images : []
      const current = urls[idx] || ''
      // ÂÜôÂÖ•ÊµèËßàÂéÜÂè≤
      try {
        const list = uni.getStorageSync('social_history') || []
        const entry = {
          key: Date.now() + '_' + String(p.id || idx),
          postId: String(p.id || ''),
          title: String(p.text || p.name || '').slice(0, 60),
          cover: current || (urls[0] || ''),
          time: 'ÂàöÂàö'
        }
        const next = [entry, ...list].slice(0, 200)
        uni.setStorageSync('social_history', next)
      } catch (e) {}
      // È¢ÑËßàÂõæÁâáÔºàÊîØÊåÅÁº©ÊîæÔºâ
      if (urls.length) {
        uni.previewImage({
          current,
          urls,
          indicator: 'number',
          loop: true
        })
      }
    },
    hasImages(p) {
      return !!(p && Array.isArray(p.images) && p.images.length > 0)
    },
    gridCols(p) {
      const len = (p && Array.isArray(p.images)) ? p.images.length : 0
      if (len <= 1) return 1
      if (len === 2) return 2
      return 3
    },
    onCreate() {
      uni.navigateTo({ url: '/pages/social/publish' })
    },
    closeAllMenus() {
      (this.posts || []).forEach(item => {
        if (item && item._menuOpen) this.$set(item, '_menuOpen', false)
      })
    },
    closeAllOverlays() {
      (this.posts || []).forEach(item => {
        if (!item) return
        if (item._menuOpen) this.$set(item, '_menuOpen', false)
        if (item._commenting) this.$set(item, '_commenting', false)
        const id = String(item.id)
        this.$set(this.focusMap, id, false)
      })
    },
    closeOverlayFor(p) {
      if (!p) return
      // Âè™ÂÖ≥Èó≠ËèúÂçïÔºå‰∏çÂΩ±ÂìçËØÑËÆ∫Ê°Ü
      this.$set(p, '_menuOpen', false)
    },
    toggleMenu(p) {
      // ÂÖàÂÖ≥Èó≠ÂÖ∂‰ªñÂ∏ñÂ≠êÁöÑËèúÂçï‰∏éËØÑËÆ∫Ê°Ü
      (this.posts || []).forEach(item => {
        if (!item) return
        if (item._menuOpen) this.$set(item, '_menuOpen', false)
        if (item._commenting) this.$set(item, '_commenting', false)
        const id = String(item.id)
        this.$set(this.focusMap, id, false)
      })
      // ÂàáÊç¢ÂΩìÂâçÂ∏ñÂ≠êÁöÑÊìç‰ΩúËèúÂçïÔºà‰ΩøÁî® $set Á°Æ‰øùÂìçÂ∫îÂºèÔºâÔºåÂπ∂Êî∂Ëµ∑ÂΩìÂâçÂ∏ñÂ≠êÁöÑËØÑËÆ∫Ê°Ü
      const next = !(p && p._menuOpen)
      this.$set(p, '_menuOpen', next)
      const pid = String(p.id)
      this.$set(this.focusMap, pid, false)
      this.$set(p, '_commenting', false)
    },
    displayTime(p) {
      const t = p && p.time
      if (!t) return 'ÂàöÂàö'
      const s = String(t).trim().toLowerCase()
      if (!s || s === 'null' || s === 'undefined') return 'ÂàöÂàö'
      return t
    },
    startComment(p) {
      if (!p) return
      this.$set(p, '_menuOpen', false)
      this.$set(p, '_commenting', true)
      const id = String(p.id)
      this.$set(this.focusMap, id, true)
    },
    endComment(p) {
      if (!p) return
      const id = String(p.id)
      // Â§±ÁÑ¶Êó∂‰ªÖÂèñÊ∂àÁÑ¶ÁÇπÔºå‰∏çÊî∂Ëµ∑ËØÑËÆ∫Ê°Ü
      this.$set(this.focusMap, id, false)
    },
    toggleLike(p) {
      if (p) p._menuOpen = false
      const key = 'social_likes'
      const favKey = 'my_fav_posts'
      const id = String(p.id)
      let arr = uni.getStorageSync(key) || []
      const set = new Set(arr.map(String))
      const before = set.has(id)
      let fav = {}
      try { fav = uni.getStorageSync(favKey) || {} } catch(e) { fav = {} }
      if (before) {
        set.delete(id)
        p.liked = false
        p.likes = Math.max((p.likes || 0) - 1, 0)
        if (fav[id]) delete fav[id]
        uni.showToast({ title: 'Â∑≤ÂèñÊ∂àÊî∂Ëóè', icon: 'none' })
      } else {
        set.add(id)
        p.liked = true
        p.likes = (p.likes || 0) + 1
        fav[id] = {
          id,
          name: p.name,
          time: p.time,
          avatar: p.avatar,
          text: p.text,
          cover: Array.isArray(p.images) && p.images.length ? p.images[0] : ''
        }
        uni.showToast({ title: 'Â∑≤Êî∂Ëóè', icon: 'none' })
      }
      arr = Array.from(set)
      uni.setStorageSync(key, arr)
      uni.setStorageSync(favKey, fav)
    },
    getComments(pid) {
      const cm = this.commentsMap || {}
      const list = cm[String(pid)] || []
      return Array.isArray(list) ? list : []
    },
    getCommentCount(pid) {
      return this.getComments(pid).length
    },
    focusComment(p) {
      this.startComment(p)
    },
    submitComment(p) {
      const pid = String(p.id)
      const text = (this.inputMap[pid] || '').trim()
      if (!text) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ËØÑËÆ∫', icon: 'none' })
        return
      }
      const one = { name: 'Êàë', text, time: 'ÂàöÂàö' }
      const cm = { ...(this.commentsMap || {}) }
      const list = Array.isArray(cm[pid]) ? cm[pid].slice() : []
      list.push(one)
      cm[pid] = list
      this.commentsMap = cm
      uni.setStorageSync('social_comments', cm)
      this.$set(this.inputMap, pid, '')
      uni.showToast({ title: 'Â∑≤ËØÑËÆ∫', icon: 'none' })
    },
    share(p) {
      if (p) p._menuOpen = false
      const link = `${this.baseUrl()}/#/pages/social/index?postId=${encodeURIComponent(p.id)}`
      uni.setClipboardData({
        data: link,
        success: () => uni.showToast({ title: 'ÈìæÊé•Â∑≤Â§çÂà∂', icon: 'none' }),
        fail: () => uni.showToast({ title: 'Â§çÂà∂Â§±Ë¥•', icon: 'none' })
      })
    },
    baseUrl() {
      try {
        if (typeof location !== 'undefined' && location.origin) return location.origin
      } catch(e) {}
      return 'https://example.com'
    }
  }
}
</script>

<style>
.page {
  background: #f7f2e7;
  position: relative;
  min-height: 100vh;
}

/* È°∂ÈÉ®Ê†è */
.header {
  height: 88rpx;
  padding: 0 24rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
}
.title {
  font-size: 36rpx;
  font-weight: 700;
  color: #1a1f36;
}
.add {
  font-size: 40rpx;
  color: #ff6a00;
}

/* ÂàóË°®‰∏éÂç°Áâá */
.feed {
  padding: 16rpx 24rpx 24rpx;
}
.post {
  background: #fff;
  border-radius: 24rpx;
  padding: 20rpx;
  margin-bottom: 20rpx;
  box-shadow: 0 10rpx 24rpx rgba(0,0,0,0.06);
}
.post { position: relative; }
.post-top {
  display: flex;
  align-items: center;
}
.avatar {
  width: 64rpx;
  height: 64rpx;
  border-radius: 50%;
  margin-right: 16rpx;
}
.author {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.name {
  font-size: 28rpx;
  color: #111827;
  font-weight: 600;
}
.time {
  font-size: 22rpx;
  color: #9ca3af;
}
.more {
  color: #9ca3af;
  font-size: 36rpx;
  padding: 0 8rpx;
}
.content {
  margin-top: 16rpx;
  font-size: 28rpx;
  color: #1f2937;
  line-height: 1.6;
}

/* ÂõæÁâáÊ†ÖÊ†º */
.images {
  margin-top: 16rpx;
  display: grid;
  grid-gap: 12rpx;
}
.images.cols-1 {
  grid-template-columns: 1fr;
}
.images.cols-2 {
  grid-template-columns: 1fr 1fr;
}
.images.cols-3 {
  grid-template-columns: 1fr 1fr 1fr;
}
.img {
  width: 100%;
  height: 300rpx;
  border-radius: 16rpx;
  background: #eee;
}

/* Êìç‰ΩúÊ†èÔºàÈöêËóèÊóßÁöÑÂ∫ïÈÉ®ÊåâÈíÆÔºå‰ªÖ‰øùÁïôÊäòÂè†ËèúÂçïÔºâ */
.actions {
  margin-top: 16rpx;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 8rpx;
  color: #6b7280;
}
.action {
  display: inline-flex;
  align-items: center;
  font-size: 26rpx;
}
.action .num {
  margin-left: 8rpx;
}
.heart {
  font-size: 28rpx;
  color: #9ca3af;
  transition: color 0.2s;
}
.heart.liked {
  color: #ff4d4f;
}

/* ËØÑËÆ∫Âå∫ */
.comments { margin-top: 12rpx; }
.c-list { display: flex; flex-direction: column; gap: 6rpx; background: #fafafa; border-radius: 12rpx; padding: 10rpx; }
.c-item { font-size: 24rpx; color: #374151; line-height: 1.4; }
.c-name { color: #1f2937; font-weight: 600; margin-right: 6rpx; }
.c-time { margin-left: 8rpx; color: #9ca3af; font-size: 22rpx; }
.c-input { margin-top: 8rpx; display: flex; gap: 8rpx; align-items: center; }
.c-input.compact { margin-top: 6rpx; }
.input { flex: 1; height: 56rpx; background: #fff; border-radius: 10rpx; padding: 0 12rpx; font-size: 24rpx; box-shadow: 0 4rpx 10rpx rgba(0,0,0,0.06); }
.input.compact { height: 52rpx; font-size: 24rpx; }
.btn.micro { height: 52rpx; line-height: 52rpx; padding: 0 14rpx; border-radius: 10rpx; background: #f3f4f6; color: #374151; font-size: 24rpx; }

/* ÊäòÂè†ËèúÂçïÔºàÁ±ª‰ººÊúãÂèãÂúàÔºâ */
.actions-menu {
  position: absolute;
  right: 16rpx;
  bottom: 120rpx;
  background: #fff;
  border-radius: 14rpx;
  box-shadow: 0 12rpx 28rpx rgba(0,0,0,0.12);
  overflow: hidden;
  min-width: 200rpx;
  z-index: 5;
}
.am-item {
  padding: 14rpx 22rpx;
  font-size: 26rpx;
  color: #1f2937;
}
.am-item + .am-item { border-top: 1rpx solid #f0f2f5; }

/* È°µÈù¢Á∫ßÈÅÆÁΩ©Ôºö‰ªª‰∏ÄËèúÂçïÂ±ïÂºÄÊó∂Âá∫Áé∞ÔºåÁÇπÂáªÂèØÂÖ≥Èó≠ÊâÄÊúâËèúÂçïÔºõ‰Ωé‰∫éËèúÂçïÁöÑ z-index */
.page-mask {
  position: fixed;
  inset: 0;
  z-index: 4; /* actions-menu ÊòØ 5 */
  background: transparent;
}

/* ËèúÂçïÈÅÆÁΩ©ÔºöË¶ÜÁõñÊï¥Âº†Âç°ÁâáÔºåÁÇπÂáªÂèØÊî∂Ëµ∑ËèúÂçï */
.menu-mask {
  position: absolute;
  inset: 0;
  z-index: 4; /* ‰Ωé‰∫é actions-menu ÁöÑ z-index:5ÔºåÈ´ò‰∫éÂç°ÁâáÂÜÖÂÆπ */
  background: transparent;
}

/* ÂõæÁâá‰∏ãÊñπ‰ø°ÊÅØË°åÔºöÂ∑¶Êó∂Èó¥„ÄÅÂè≥ÁúÅÁï•Âè∑ */
.img-footer {
  margin-top: 8rpx;
  padding: 0 6rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.time-badge {
  background: transparent;
  color: #9ca3af;
  font-size: 24rpx;
  padding: 0;
}
.menu-dot {
  color: #9ca3af;
  font-size: 32rpx;
  padding: 4rpx 8rpx;
  border-radius: 8rpx;
}

/* Âè≥‰∏äËßíÂç°ÈÄöÂõæÊ†∑ÂºèÔºàÁªü‰∏ÄÔºâ */
</style>